import { getDateFromSILKDate } from '../utils/date-helper';

export function adaptEventItems(eventList) {
  if (
    !eventList.listing ||
    !eventList.listing.listing ||
    !eventList.listing.listing.asset
  )
    return [];

  let newItemsList = [
    {
      id: 0,
      startDate: getDateFromSILKDate({ date: '2016/10/10', time: '00:00' }),
      endDate: getDateFromSILKDate({ date: '2016/10/10', time: '23:59' }),
      title: 'Sky Sport News HD',
      imgUrl:
        'https://www.skygo.sky.de/sg/bin/EPGChannel/logos/channel_114_324x198.png',
      streamUrl:
        'http://livemss1it-s.akamaihd.net/live/242816_242817/ssn-hd.isml/Manifest',
      apixId: 0,
      eventId: 0
    }
  ];
  const items = eventList.listing.listing.asset;

  for (let i = 0; i < items.length; i++) {
    const silkStartDate = items[i].technical_event.on_air.start_date;
    const silkStartTime = items[i].technical_event.on_air.start_time;
    const silkEndDate = items[i].technical_event.on_air.end_date;
    const silkEndTime = items[i].technical_event.on_air.end_time;
    const startDate = getDateFromSILKDate({
      date: silkStartDate,
      time: silkStartTime
    });
    const endDate = getDateFromSILKDate({
      date: silkEndDate,
      time: silkEndTime
    });

    let ticketEndDate = new Date();
    ticketEndDate.setDate(new Date().getDate() + 1);
    ticketEndDate.setHours(7);
    ticketEndDate.setMinutes(25);
    ticketEndDate.setSeconds(0);

    if (endDate.getTime() > ticketEndDate.getTime()) continue;

    const id = items[i].id;
    const title = items[i].title;
    const streamUrl = items[i].media_url_hls;
    const eventId = items[i].event_id;
    const apixId = items[i].apix_id;

    let imgUrl = '';

    for (let ii = 0; ii < items[i].main_picture.picture.length; ii++) {
      const img = items[i].main_picture.picture[ii];
      if (img.width > img.height) {
        if (img.width > 316 && img.height > 177) {
          imgUrl =
            'https://skyticket.sky.de' +
            items[i].main_picture.picture[ii].path +
            '/' +
            items[i].main_picture.picture[ii].file;
          break;
        }
      }
    }

    newItemsList.push({
      id,
      startDate,
      endDate,
      title,
      imgUrl,
      streamUrl,
      apixId,
      eventId
    });
  }

  return newItemsList;
}



