import * as types from './event-list-action-types'
import axios from 'axios';

export function getEventList(url) {
  const request = axios.get(url);

  return (dispatch) => {
    request.then(({data}) => {

      dispatch({type: types.GET_EVENT_LIST, payload: data})
    });
  }
}

export function setHistoryEventList(list) {
  return {
    type: types.GET_EVENT_LIST_FROM_HISTORY,
    payload : list
  }
}

export function setSelectedEventListItem(index) {
  return {
    type: types.SET_SEL_EVENT_LIST_INDEX,
    index : index
  }
}


