import * as types from './login-action-types'
import * as cloudExt from '../../vendor/cloud-ext';
import axios from 'axios';

let pinCache;
let autologinCache = false;

export function login(url, user, password, deviceId, autoLogin, callback) {

  console.log('>>>>>>> ---------------------------------------------------------');
  console.log('>>>>>>> login-action-creators.js :: user: ', user);
  console.log('>>>>>>> login-action-creators.js :: password: ', password);
  console.log('>>>>>>> login-action-creators.js :: deviceId: ', deviceId);
  console.log('>>>>>>> login-action-creators.js :: autoLogin: ', autoLogin);
  console.log('>>>>>>> ---------------------------------------------------------');

  pinCache = password;
  autologinCache = autoLogin;

  const request = axios.get(url, {
    params: {
      customerCode: user,
      password: password,
      product: "ST",
      platform: "web",
      version: "1234"
    }
  });

  return (dispatch) => {
    request.then(({data}) => {
      // eslint-disable-next-line
      eval(data);
      // eslint-disable-next-line
      function fn(data) {
        if(data.resultCode === "T_206") {
          console.log('---> login-action-creators.js --- T_206 --- User already logged in. Force logout');
          axios.post(`https://skyticket.sky.de/SILK/services/public/session/kill?skygoSessionId=${data.skygoSessionId}&product=ST&platform=tv&version=0.1b`).then(({data}) => {
            axios.get(url, {
              params: {
                customerCode: user,
                password: password,
                deviceId: deviceId,
                product: "ST",
                platform: "web",
                version: "1234"
              }
            }).then(({data}) => {
              // eslint-disable-next-line
              eval(data);
              // eslint-disable-next-line
              function fn(data) {
                if(data.resultCode === "S_218") {
                  console.log('---> login-action-creators.js --- S_218 --- skygoSessionId error');
                  axios.get(url, {
                    params: {
                      customerCode: user,
                      password: password,
                      deviceId: deviceId,
                      product: "ST",
                      platform: "web",
                      version: "1234"
                    }
                  }).then(({data}) => {
                    // eslint-disable-next-line
                    eval(data);
                    // eslint-disable-next-line
                    function fn(data) {
                      if (!autoLogin) storeCredentials(data);
                      data.isRecoveredState = autologinCache;
                      data.pin = pinCache;
                      callback(data);
                      dispatch({type: types.SET_LOGIN, payload: data})
                    }
                  });
                } else {
                  if (!autoLogin) storeCredentials(data);
                  data.isRecoveredState = autologinCache;
                  data.pin = pinCache;
                  callback(data);
                  dispatch({type: types.SET_LOGIN, payload: data})
                }
              }
            });
          });
        } else {
          if (!autoLogin) storeCredentials(data);
          data.isRecoveredState = autologinCache;
          data.pin = pinCache;
          callback(data);
          dispatch({type: types.SET_LOGIN, payload: data})
        }
      }
    });
  }
}

function storeCredentials(data) {
  if (data.resultCode !== "T_100") return;
  cloudExt.setUserInfo({
    user: data.customerCode,
    pin: pinCache
  })
}

export function setHistoryLogin(login) {
  return {
    type: types.SET_LOGIN,
    payload : login
  }
}

export function setDeviceId(deviceId) {
  return {
    type: types.SET_DEVICE_ID,
    deviceId: deviceId
  }
}


