import React from 'react';

class RemoteDPadMapComponent extends React.Component {
  render() {
    const base = this.props.digitsToggled ? 5 : 0;

    return (
      <div className="RemoteDPadMap-container">
        <div className="RemoteDPadMap-row RemoteDPadMap-top">
          <div className="RemoteDPadMap-top RemoteDPadMap-left" />
          <div className="RemoteDPadMap-top RemoteDPadMap-center">
            {base + 1}
          </div>
          <div className="RemoteDPadMap-top RemoteDPadMap-right" />
        </div>
        <div className="RemoteDPadMap-row RemoteDPadMap-middle">
          <div className="RemoteDPadMap-middle RemoteDPadMap-left">
            {base + 4}
          </div>
          <div className="RemoteDPadMap-middle RemoteDPadMap-center">
            {base + 0}
          </div>
          <div className="RemoteDPadMap-middle RemoteDPadMap-right">
            {base + 2}
          </div>
        </div>
        <div className="RemoteDPadMap-row RemoteDPadMap-bottom">
          <div className="RemoteDPadMap-bottom RemoteDPadMap-left" />
          <div className="RemoteDPadMap-bottom RemoteDPadMap-center">
            {base + 3}
          </div>
          <div className="RemoteDPadMap-bottom RemoteDPadMap-right" />
        </div>
      </div>
    );
  }
}

export default RemoteDPadMapComponent;



