import { combineReducers } from 'redux';
import * as core from '../container/stage/stage-reducers';
import * as login from '../container/login/login-reducers';
import * as eventList from '../container/event-list/event-list-reducers';
import * as purchase from '../container/purchase/purchase-reducers';
import * as video from '../container/video/video-reducers';
import * as notification from '../container/notification/notification-reducers';

export default combineReducers(
  Object.assign(core, login, eventList, purchase, video, notification)
);



