import React from 'react';
import DigitComponent from './digit-component'

class DigitsComponent extends React.Component {

  getContent(i) {
    if (String(this.props.digits).charAt(i)) {
      if (this.props.isPassword) return "*";
      else return String(this.props.digits).charAt(i);
    } else return '';
  }

  render() {

    let fields = [];

    for (let i = 0; i < this.props.numberOfDigits; i++) {
      fields.push(
        <DigitComponent
          key={i}
          cursorOffset={this.props.digitsEntered-i}
          enteredDigit={this.getContent(i)}
          numberOfDigits={this.props.numberOfDigits}
        />)
    }

    return (
      <div className="Digits-container">
        {fields}
      </div>
    );
  }
}

export default DigitsComponent;



