import React from 'react';

class DigitComponent extends React.Component {
  render() {
    let stateClass;
    if (this.props.cursorOffset > 0) {
      stateClass = 'Digit-filled-' + this.props.numberOfDigits;
    } else if (this.props.cursorOffset === 0) {
      stateClass = 'Digit-cursor';
    } else {
      stateClass = 'Digit-unfilled';
    }

    return (
      <div className={['Digit-container-' + this.props.numberOfDigits, stateClass].join(' ')}>
        {this.props.enteredDigit}
      </div>
    );
  }
}

export default DigitComponent;



