export function adaptTickets(tickets) {
  const ticketsList = tickets.daypass;
  const today = new Date();

  const day = today.getDate() < 10 ? '0' + today.getDate() : today.getDate();
  const monthNum = today.getMonth() + 1;
  const month = monthNum < 10 ? '0' + monthNum : monthNum;

  const searchString = `Supersport Tagesticket ${day}.${
    month
  }.${today.getFullYear()}`;

  for (let i = 0; i < ticketsList.length; i++) {
    if (ticketsList[i].title.indexOf(searchString) !== -1) {
      ticketsList[i].validity.start_date = replaceAll(
        ticketsList[i].validity.start_date,
        '/',
        ''
      );
      ticketsList[i].validity.end_date = replaceAll(
        ticketsList[i].validity.end_date,
        '/',
        ''
      );

      ticketsList[i].validity.start_time =
        ticketsList[i].validity.start_time.replace(':', '') + '00';
      ticketsList[i].validity.end_time =
        ticketsList[i].validity.end_time.replace(':', '') + '00';

      return ticketsList[i];
    }
  }

  return {};
}

function replaceAll(target, search, replacement) {
  return target.split(search).join(replacement);
}



