import React, { Component } from 'react';
import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'
import {appStates} from '../../utils/app-state-symbols';

import EventListComponent from '../../components/event-list/event-list-component'

class EventListContainer extends Component {
  constructor(props) {
    super(props);
    this.onKey = this.onKey.bind(this);
  }
  componentWillMount() {
    window.addEventListener("keydown", this.onKey, false);

    if (this.ticketIsPurchased()) this.props.setSelectedEventListItem(0);
    else this.props.setSelectedEventListItem(-1);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.onKey, false);
  }

  ticketIsPurchased() {
    return this.props.isTicketPurchased || ((this.props.userData.tickets && this.props.userData.tickets.length > 0) || (this.props.userData.dayPasses && this.props.userData.dayPasses.length > 0))
  }

  onKey(event) {
    if (event.keyCode === 37 || event.keyCode === 39) return;

    // Up
    if (event.keyCode === 38 && this.props.selectedEventListItem > (this.props.isTicketPurchased ? 0 : -1)) {
      this.props.setSelectedEventListItem(this.props.selectedEventListItem - 1);
    }

    // Down
    if (event.keyCode === 40 && this.props.selectedEventListItem < (this.props.eventList.length-1)) {
      this.props.setSelectedEventListItem(this.props.selectedEventListItem + 1);
    }

    // Enter
    if (event.keyCode === 13 && (this.props.purchasableTicket && this.props.purchasableTicket.cms_id)) {
      if (this.props.selectedEventListItem === 0) {
        this.props.getSSNHD(this.props.config.Endpoints.SSNHD);
        this.props.setAppState(appStates.LOADING, this.props);
      }
      else if (this.props.isTicketPurchased) {
        this.props.setCurrentAsset(this.props.eventList[this.props.selectedEventListItem], this.props.channelList);
        this.props.setAppState(appStates.LOADING, this.props);
      }
      else this.props.setAppState(appStates.ASKING_PIN, this.props);
    }

    // Backspace
    if (event.keyCode === 8) {
      this.props.setAppState(appStates.PLAYBACK, this.props)
    }
  }

  render() {

    if (!this.props.ticketIsPurchased &&
      ((this.props.userData.tickets && this.props.userData.tickets.length > 0) ||
      (this.props.userData.dayPasses && this.props.userData.dayPasses.length > 0))){
      this.props.setTicketPurchased();
    }

    return (
      <EventListComponent
        eventList={this.props.eventList}
        selectedIndex={this.props.selectedEventListItem}
        ticketAquired={this.ticketIsPurchased()}
        ticketAvalible={this.props.purchasableTicket && this.props.purchasableTicket.cms_id}
      />
    );
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(EventListContainer);



