import React, { Component } from 'react';
import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'
import {appStates} from '../../utils/app-state-symbols';

import DigitEntryComponent from '../../components/digit-entry/digit-entry-component';

class PurchaseContainer extends Component {
  constructor(props) {
    super(props);

    this.pinEnteredHandler = this.pinEnteredHandler.bind(this);
    this.backHandler = this.backHandler.bind(this);
  }

  componentDidUpdate() {
    if (this.props.isTicketPurchased) {
      this.props.showPurchaseNotification();
      this.props.setAppState(appStates.PLAYBACK, this.props);
    }
  }

  pinEnteredHandler(pin) {
    if (String(pin) === String(this.props.userData.pin)) {
      this.props.purchaseTicket(
        this.props.config.Endpoints.SILK.domain + this.props.config.Endpoints.SILK.purchase,
        this.props.userData.skygoSessionId,
        this.props.purchasableTicket,
        this.purchaseHandler
      );
    } else {
      //TODO: Pin Entry Error
    }
  }

  purchaseHandler(purchaseSuccess) {
    
    console.log('---> purchase-container.js at Line 47 :: purchaseSuccess >>', purchaseSuccess);
    
    if (purchaseSuccess) {
      //TODO: Confirm
    } else {
      //TODO: Purchase Error
    }
  }

  backHandler() {
    this.props.setAppState(appStates.SHOWING_EVENTS);
  }

  render() {
    return (
      <DigitEntryComponent
        caption={"Bitte bestätigen Sie Ihre Bestellung mit Ihrer Sky Business PIN."}
        digitsEnteredHandler={this.pinEnteredHandler}
        backHandler={this.backHandler}
        numberOfDigits={4}
        isPassword={true}

      />
    );
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(PurchaseContainer);



