import React from 'react';
import RemoteDPadMapComponent from './remote-dpad-component';
import ToggleIndicatorComponent from './toggle-indicator-component';

class RemoteMapComponent extends React.Component {
  render() {
    return (
      <div className="RemoteMap-container">
        <RemoteDPadMapComponent digitsToggled={this.props.digitsToggled} />
        <ToggleIndicatorComponent digitsToggled={this.props.digitsToggled} />
      </div>
    );
  }
}

export default RemoteMapComponent;



