import React from 'react';
import ticket from './../../assets/img/ticket.png';

class LoadingScreenComponent extends React.Component {
  render() {
    return (
      <div className={'LoadingScreen-container LoadingScreen-event'} >
        <img src={ticket} className="LoadingScreen-ticket" alt="ticket" />
        {this.props.eventIsLoading ? <p className="LoadingScreen-caption">Ihr Event startet in Kürze.</p> : <p className="LoadingScreen-caption">Gleich gehts los</p>}
      </div>
    );
  }
}

export default LoadingScreenComponent;



