import React from 'react';

import EventListBannerComponent from './event-list-banner-component';
import EventListItemComponent from './event-list-item-component';

class EventListComponent extends React.Component {
  getHeader() {
    if (!this.props.ticketAquired) {
      return ([
        <EventListBannerComponent
          key={0}
          selected={this.props.selectedIndex === -1}
          ticketAvalible={this.props.ticketAvalible}
        />,
        <div key={1} className="EventList-headline">Enthaltene Live Events</div>
      ]);
    } else return null;
  }

  render() {
    return (
      <div className="EventList-container">
        {this.getHeader()}
        <div
          className="EventList-scrollcontainer"
          ref={scrollEl => (this._scrollEl = scrollEl)}
        >
          {this.props.eventList.map((event, i) => {
            return (
              <EventListItemComponent
                key={i}
                index={i}
                event={event}
                selected={i === this.props.selectedIndex}
                ticketAquired={this.props.ticketAquired}
                ticketAvalible={this.props.ticketAvalible}
              />
            )
          }
          )}
        </div>
      </div>
    );
  }

  componentDidUpdate() {
    const childIndex = this.props.selectedIndex  - (this.props.ticketAquired ? 0 : 1);
    
    if (this._scrollEl) {
      this._scrollEl.children[Math.max(childIndex, 0)].scrollIntoView();
    }
  }
}

export default EventListComponent;


