import React, { Component } from 'react';
import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'
import {notifications} from '../../utils/notification-symbols';

import PurchaseNotification from '../../components/notifications/purchase-notification-component'
import MinizapperNotification from '../../components/notifications/minizapper-notification-component'
import LoginErrorNotification from '../../components/notifications/loginerror-notification-component'

class NotificationContainer extends Component {
  _timeoutId = null;

  constructor(props) {
    super(props);
    this.onKey = this.onKey.bind(this);
  }

  getNotification() {
    if (this.props.notification) {
      if (this.props.notification === notifications.PURCHASE_NOTIFICATION) {
        this._timeoutId = setTimeout(() => {this.hideNotification()}, 8000);
        window.addEventListener("keydown", this.onKey, false);
        return <PurchaseNotification />
      }

      if (this.props.notification === notifications.MINIZAPPER_NOTIFICATION) {
        this._timeoutId = setTimeout(() => {this.hideNotification()}, 8000);
        return <MinizapperNotification currentAsset={this.props.currentAsset} />
      }

      if (this.props.notification.indexOf('LOGIN_ERROR') !== -1) {
        // this._timeoutId = setTimeout(() => {this.hideNotification()}, 15000);
        return <LoginErrorNotification errorCode={this.props.notification.substring('LOGIN_ERROR_'.length)} />
      }
    }
  }

  hideNotification() {
    this.props.hideNotification();
    window.removeEventListener("keydown", this.onKey, false);
  }

  onKey() {
    clearTimeout(this._timeoutId);
    this._timeoutId = null;
    this.hideNotification();
  }

  render() {
    return (
      <div className="notification-container">
        { this.getNotification() }
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}


export default connect(mapStateToProps, mapDispatchToProps)(NotificationContainer);



