import createReducer from '../../lib/create-reducer'
import * as types from './purchase-action-types'
import { adaptTickets } from '../../adapters/ticktes-adapter'

export const isTicketPurchased = createReducer(false, {
  [types.SET_TICKET_PUCHASED](state, action) {
    return action.payload
  },
});

export const purchasableTicket = createReducer([], {
  [types.GET_TICKETS_LIST](state, action) {
    return adaptTickets(action.payload);
  },
});


