import React from 'react';
import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'
import {appStates} from '../../utils/app-state-symbols';
import VideoComponent from '../../components/video/video-component'

class VideoContainer extends React.Component {
  constructor(props) {
    super(props);

    this.errorCallback = this.errorCallback.bind(this);
  }

  errorCallback(e) {
    switch (e.target.error) {
      case 218:
        this.props.setAppState(appStates.ERROR)
        break;
      case 229:
        this.props.setAppState(appStates.ERROR)
        break;
      case 230:
        this.props.setAppState(appStates.ERROR)
        break;
      case 1:
      case 2:
      case 3:
      case 4:
        this.props.setAppState(appStates.ERROR)
        break;
      default:
        break;
    }
  }

  render() {
    return (
      <VideoComponent
        skygoSessionId={this.props.userData.skygoSessionId}
        deviceId={this.props.deviceId}
        currentAsset={this.props.currentAsset}
        isRecoveredState={this.props.userData.isRecoveredState}
        errorCallback={this.errorCallback}
      />
    );
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(VideoContainer);



