import React, { Component } from 'react';

import DigitsComponent from './digits-component';
import RemoteMapComponent from './remote-map-component';

class DigitEntryComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      digitsToggled: false,
      digitsEntered: 0,
      digits: ""
    };

    this.onKey = this.onKey.bind(this);
  }

  componentDidMount() {
    window.addEventListener("keydown", this.onKey, false);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.onKey, false);
  }

  onKey(event) {
    // Back
    if (event.keyCode === 8) {
      if (this.state.digitsEntered === 0) {
        this.props.backHandler();
      } else {
        this.setState({
          digitsEntered: this.state.digitsEntered-1,
          digits: (this.state.digitsEntered === 1) ? "" : Math.floor(this.state.digits / 10)
        });
      }
      return;
    }

    // Menu
    if (event.keyCode === 220) {
      this.setState({
          digitsToggled: !this.state.digitsToggled
      });
      return;
    }

    // Down
    if (event.keyCode === 40) {
      this.digitEntered(this.state.digitsToggled ? 8 : 3);
    }

    // Up
    if (event.keyCode === 38) {
      this.digitEntered(this.state.digitsToggled ? 6 : 1);
    }

    // Right
    if (event.keyCode === 39) {
      this.digitEntered(this.state.digitsToggled ? 7 : 2);
    }

    // Left
    if (event.keyCode === 37) {
      this.digitEntered(this.state.digitsToggled ? 9 : 4);
    }

    // Enter
    if (event.keyCode === 13) {
      this.digitEntered(this.state.digitsToggled ? 5 : 0);
    }
  }

  digitEntered(digit) {
    this.setState({
      digitsEntered: this.state.digitsEntered + 1,
      digits: "" + this.state.digits + digit
    });
    if (this.state.digits.length === this.props.numberOfDigits) {
      const digits = this.state.digits;
      this.setState({
        digits:'',
        digitsEntered: 0
      });
      this.props.digitsEnteredHandler(digits);
    }
  }

  render() {
    return (
      <div className="PinEntry-overlay">
        <div className="PinEntry-container">
          <h1>
            {this.props.caption}
          </h1>
          <DigitsComponent
            isPassword={this.props.isPassword}
            digits={this.state.digits}
            digitsEntered={this.state.digitsEntered}
            numberOfDigits={this.props.numberOfDigits}
          />
          <RemoteMapComponent digitsToggled={this.state.digitsToggled} />
        </div>
      </div>
    );
  }
}

export default DigitEntryComponent;



