import createReducer from '../../lib/create-reducer'
import * as types from './video-action-types'
import { adaptEventDetailItem } from '../../adapters/event-detail-adapter'
import { adaptChannels } from '../../adapters/channel-adapter'

export const currentAsset = createReducer({}, {
  [types.SET_ASSET](state, action) {
    return adaptEventDetailItem(action.payload);
  },
  [types.SET_ASSET_FROM_HISTORY](state, action) {
    return action.payload;
  },
  [types.LOAD_SSNHD](state, action) {
    return {
      startDate: null,
      endDate: null,
      title: 'Sky Sport News HD',
      imgUrl: 'https://www.skygo.sky.de/sg/bin/EPGChannel/logos/channel_114_324x198.png',
      streamUrl: "http://livemss1it-s.akamaihd.net/live/242816_242817/ssn-hd.isml/Manifest",
      apixId: action.payload.id,
      assetId: 17
    }
  }
});

export const channelList = createReducer([], {
  [types.LOAD_CHANNEL_LIST](state, action) {
    return adaptChannels(action.payload);
  },
});


