import createReducer from '../../lib/create-reducer'
import * as types from './stage-action-types'
import {appStates} from '../../utils/app-state-symbols';

export const appState = createReducer(appStates.IDLE, {
  [types.SET_APP_STATE](state, action) {
    return action.state
  },
});

export const config = createReducer({},  {
  [types.SET_CONFIGURATION](state, action) {
    return action.config
  },
});


