import createReducer from '../../lib/create-reducer'
import * as types from './login-action-types'

export const userData = createReducer([], {
  [types.SET_LOGIN](state, action) {
    return action.payload;
  },
});

export const deviceId = createReducer(null, {
  [types.SET_DEVICE_ID](state, action) {
    return action.deviceId;
  },
});


