import React from 'react';
import ticket from './../../assets/img/ticket.png';

class SplashScreenComponent extends React.Component {
  render() {
    return (
      <div className={'LoadingScreen-container LoadingScreen-event'} >
        <img src={ticket} className="LoadingScreen-ticket" alt="ticket" />
          <p className="LoadingScreen-caption">Gleich gehts los</p>
      </div>
    );
  }
}

export default SplashScreenComponent;



