import React, { Component } from 'react';
import PropTypes from 'prop-types';
import invariant from 'invariant';

const defaultPlaybackInfo = { progress: NaN };

const _contextTypes = {
  _videoInstance: PropTypes.any.isRequired
};

class VideoComponent extends Component {
  constructor() {
    super();
    this._videoRef = null;
    this._activeWrappers = [];
    this._latestPlaybackInfo = defaultPlaybackInfo;
  }

  _setupVideoRef = videoRef => {
    if (!videoRef) {
      this._cleanupVideoRef();
      return;
    }
    this._videoRef = videoRef;
    this._updateWrappers();
    videoRef.addEventListener('timeupdate', this._updateWrappers);
  };

  _cleanupVideoRef = () => {
    if (!this._videoRef) {
      return;
    }
    this._videoRef.removeEventListener('timeupdate', this._updateWrappers);
    this._videoRef = null;
    this._latestPlaybackInfo = defaultPlaybackInfo;
  };

  _updateWrappers = () => {
    const progress = this._videoRef.currentTime / this._videoRef.duration;
    const stateUpdate = (this._latestPlaybackInfo = { progress });
    this._activeWrappers.forEach(wrapper => wrapper.setState(stateUpdate));
  };

  _registerWrapper(wrapper) {
    invariant(
      this._activeWrappers.indexOf(wrapper) < 0,
      'Wrapper is already registered'
    );
    this._activeWrappers.push(wrapper);
    wrapper.setState(this._latestPlaybackInfo);
  }

  _unregisterWrapper(wrapper) {
    const wrapperIdx = this._activeWrappers.indexOf(wrapper);
    invariant(wrapperIdx >= 0, 'Wrapper is not registered');
    this._activeWrappers.splice(wrapperIdx, 1);
  }

  componentWillUnmount() {
    this._cleanupVideoRef();
  }

  properVideoContent(assetPath, vodInfo) {
    if (this.props.isRecoveredState) {
      return (
        <video
          className="Video-player"
          width="1280"
          height="720"
          cloud-info={JSON.stringify(vodInfo)}
          src={assetPath}
          ref={this._setupVideoRef}
          key={assetPath}
          autoPlay
          loop
          cloud-recovery="true"
          cloud-live="true"
          onError={this.props.errorCallback}
        />
      )
    } else {
      return(
        <video
          className="Video-player"
          width="1280"
          height="720"
          cloud-info={JSON.stringify(vodInfo)}
          src={assetPath}
          ref={this._setupVideoRef}
          key={assetPath}
          autoPlay
          loop
          cloud-live="true"
          onError={this.props.errorCallback}
        />
      )
    }
  }

  render() {
    let assetPath = this.props.currentAsset.streamUrl;

    const vodInfo = {
      skygoSessionId: this.props.skygoSessionId,
      apixId: this.props.currentAsset.apixId,
      assetId: this.props.currentAsset.assetId,
      deviceId: this.props.deviceId,
      platformId: 'web',
      product: 'ST',
      version: 1234
    };
    
    return (
      <div className="Video-container">
        {this.properVideoContent(assetPath, vodInfo)}
      </div>
    );
  }

  getChildContext() {
    return { _videoInstance: this };
  }
}

VideoComponent.childContextTypes = _contextTypes;
export default VideoComponent;

/**
 * Wraps the passed Component so it receives playbackInfo via props.
 * Instances of the returned Component must be decendants of a Video instance.
 *
 * @param WrappedComponent The component to be wrapped
 * @return A HOC that injects playbackInfo into the props of WrappedComponent
 */
const injectPlaybackInfo = WrappedComponent => {
  class PlaybackInfoWrapper extends Component {
    state = defaultPlaybackInfo;

    componentWillMount() {
      this.context._videoInstance._registerWrapper(this);
    }

    render() {
      return <WrappedComponent playbackInfo={this.state} {...this.props} />;
    }

    componentWillUnmount() {
      this.context._videoInstance._unregisterWrapper(this);
    }
  }
  PlaybackInfoWrapper.contextTypes = _contextTypes;
  return PlaybackInfoWrapper;
};
export { injectPlaybackInfo };



