import React, { Component } from 'react';
import textBanner from './../../assets/img/text-banner.png';

class PurchaseNotification extends Component {
  render() {
    return (
      <div className="PurchaseNotification-scrim">
        <div className="PurchaseNotification-container">
          <div className="PurchaseNotification-header">
            <img className="PurchaseNotification-textbanner" src={textBanner} alt=""/>
          </div>
          <div className="PurchaseNotification-body">
            <div className="PurchaseNotification-headline">
              Kauf erfolgreich
            </div>
            Ab sofort bis morgen früh 6:50 Uhr haben Sie Zugriff auf den
            gesamten Live Sport von Sky. Drücken Sie eine beliebige Taste, um
            ein Event auszuwählen.
            <div className="PurchaseNotification-button">
              Zur Eventübersicht
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PurchaseNotification;



