import React from 'react';

class EventListBannerComponent extends React.Component {
  bannerConntent() {

    const today = new Date();
    let tomorrow = new Date(new Date().setDate(today.getDate() + 1));

    const friendlyToday = `${today.getDate()}.${today.getMonth()+1}.${today.getFullYear()}`;
    const friendlyTomorrow = `${tomorrow.getDate()}.${tomorrow.getMonth()+1}.${tomorrow.getFullYear()}`;

    if (this.props.selected) {
      return (
        <div className="EventListItem-info TicketItem-info">
          <div className="TicketItem-headline">Sky Business Tagesticket</div>
          <div className="TicketItem-body">
            <span className="TicketItem-price">€ 99 </span>
            <span className="TicketItem-once"> einmalig</span>
            <span className="TicketItem-validity">
              {' '}| {`Gültig ${friendlyToday} 05:30 Uhr bis ${friendlyTomorrow} 07:25 Uhr`}
            </span>
          </div>
          <span className="EventListItem-order">{(this.props.ticketAvalible) ? 'Jetzt buchen!' : 'Kein Tagesticket verfügbar!'}</span>
        </div>
      )
    } else {
      return (
        <div className="EventListItem-info TicketItem-info">
          <div className="TicketItem-headline">
            Sky Business Tagesticket für € 99{' '}
            <span className="TicketItem-once">einmalig</span>
          </div>
        </div>
      )
    }
  }

  render() {
    return (
      <div className={`EventListItem-container TicketItem-container ${this.props.selected ? 'selected' : ''}`} >
        <div className="EventListItem-graphic TicketItem-graphic" />
        { this.bannerConntent() }
      </div>
    );
  }
}

export default EventListBannerComponent;



