import React from 'react';
import {appStates} from '../../utils/app-state-symbols';
import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'
import * as cloudExt from '../../vendor/cloud-ext';
import { getDateFromSILKDate } from '../../utils/date-helper'

import VideoContainer from '../video/video-container';
import LoginContainer from '../login/login-container';
import ErrorScreenContainer from '../error-screen/error-screen-container';
import SplashScreenContainer from '../splash-screen/splash-screen-container';
import LoadingScreenContainer from '../loading-screen/loading-screen-container';
import NotificationContainer from '../notification/notification-container';
import EventListContainer from '../event-list/event-list-container';
import PurchaseContainer from '../purchase/purchase-container';

import history from '../../assets/saved_state.json';

class StageContainer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      history: ''
    }

    this.onKey = this.onKey.bind(this);
    this.startApplication = this.startApplication.bind(this);
    this.checkLogin = this.checkLogin.bind(this);
    this.loginCallback = this.loginCallback.bind(this);
    this.deviceIdReceived = this.deviceIdReceived.bind(this);
    this.saveStateToPersistantStore = this.saveStateToPersistantStore.bind(this);
  }

  componentDidMount() {
    this.props.loadConfiguration('PROD');
    this.props.setAppState(appStates.BOOTING);

    if (cloudExt.isCloud()) {
      cloudExt.addEventListener('onTimeout', this.saveStateToPersistantStore);
      cloudExt.addEventListener('onMove', this.startApplication);
    } else {
      // this.props.setAppState(appStates.LOGIN, this.props);
      this.startApplication({history: ''});
      // this.startApplication({history: JSON.stringify(history)})
    }
  }

  componentWillUpdate(nextProps, nextState) {
    if (nextProps.appState === appStates.PLAYBACK) window.addEventListener("keydown", this.onKey, false);
    else window.removeEventListener("keydown", this.onKey, false);
  }

  onKey(event) {
    if (event.keyCode === 13) {
      this.props.setAppState(appStates.SHOWING_EVENTS);
    }
  }

  saveStateToPersistantStore() {
    this.props.saveStateToPersistantStore(this.props);
  }

  startApplication(state) {
    this.setState(((state.history !== '') ? JSON.parse(state.history) : {history: ''}));
    cloudExt.getDeviceId(this.deviceIdReceived);
  }

  deviceIdReceived(deviceId) {
    this.props.getEventList(this.props.config.Endpoints.Events);
    this.props.getChannelList(this.props.config.Endpoints.ChannelList);
    this.props.getAvalibleTicket(this.props.config.Endpoints.Tickets);
    this.props.setDeviceId(deviceId);

    if (this.state && this.state && this.state.appState) {

      const savedState = this.state;

      savedState.userData.isRecoveredState = true;
      if (savedState.currentAsset) this.props.setCurrentAssetfromHistory(savedState.currentAsset);
      if (savedState.isTicketPurchased) this.props.setTicketPurchased();
      if (savedState.userData) this.props.setHistoryLogin(savedState.userData);
      return;
    } else {
      this.props.getSSNHD(this.props.config.Endpoints.SSNHD)
    }

    cloudExt.getUserInfo(this.checkLogin);
  }

  checkLogin(userInfo) {
    // userInfo.user = 5449144998;
    // userInfo.pin = 2020;

    // userInfo.user = 2249683183;
    // userInfo.pin = 2016;

    if (userInfo.user && userInfo.pin)
    {
      let loginPath = this.props.config.Endpoints.SILK.domain + this.props.config.Endpoints.SILK.login;
      this.props.login(loginPath, userInfo.user, userInfo.pin, this.props.deviceId, false, this.loginCallback);
    } else {
      this.props.setAppState(appStates.LOGIN, this.props);
    }
  }

  loginCallback(result) {
    if (result.resultMessage !== 'OK') this.props.setAppState(appStates.LOGIN, this.props);
    else this.props.setAppState(appStates.LOADING, this.props);
  }

  render() {
    if (this.props.appState === appStates.IDLE) return null;

    const state = this.props.appState;
    let isReady = false;

    if (this.state && this.state.appState !== '' && this.props.appState === appStates.BOOTING) {
      if (
        (JSON.stringify(this.props.currentAsset) !== JSON.stringify({})) &&
        (this.props.isTicketPurchased === this.state.isTicketPurchased) &&
        (JSON.stringify(this.props.userData) === JSON.stringify(this.state.userData)) &&
        (this.props.eventList.length > 0)
      ) {
        isReady = true;
        if (this.state.appState !== appStates.SHOWING_EVENTS) this.props.setAppState(this.state.appState);
        else this.props.setAppState(appStates.PLAYBACK);
      }
    } else if(this.props.appState === appStates.BOOTING) {
      isReady = true;
      this.props.setAppState(appStates.LOGIN);
    } else {
      isReady = true;
    }

    if (isReady) {
      return (
        <div className="Stage">
          {state === appStates.LOGIN ? <LoginContainer /> : null }
          {state !== appStates.BOOTING && state !== appStates.LOGIN ? <VideoContainer /> : null }
          {state === appStates.SHOWING_EVENTS ? <EventListContainer /> : null}
          {state === appStates.ASKING_PIN ? <PurchaseContainer /> : null}
          <NotificationContainer />
          {(state === appStates.LOADING) ? <LoadingScreenContainer /> : null}
          {(state === appStates.BOOTING) ? <SplashScreenContainer /> : null}
          {(state === appStates.ERROR) ? <ErrorScreenContainer /> : null}
        </div>
      );
    } else {
      return (
        <div className="Stage">
          <SplashScreenContainer />
        </div>
      );
    }
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(StageContainer);



