import React from 'react';

class ToggleIndicatorComponent extends React.Component {
  render() {
    const base = this.props.digitsToggled ? 0 : 5;

    return (
      <div className="ToggleIndicator-container">
      <span className="ToggleIndicator-icon">
        <div className="ToggleIndicator-bar" />
        <div className="ToggleIndicator-bar" />
        <div className="ToggleIndicator-bar" />
      </span>
        <span className="ToggleIndicator-text">
        {base} - {base + 4}
      </span>
      </div>
    );
  }
}

export default ToggleIndicatorComponent;



