import React from 'react';
import ticket from './../../assets/img/ticket.png';

class ErrorScreenComponent extends React.Component {
  render() {
    return (
      <div className={'LoadingScreen-container LoadingScreen-event'} >
        <img src={ticket} className="LoadingScreen-ticket" alt="ticket" />
          <p className="LoadingScreen-caption">Fehler!</p>
      </div>
    );
  }
}

export default ErrorScreenComponent;



