export function adaptChannels(channelList) {
  let newChannelList = [];
  const channels = channelList.channelList;

  for (let i = 0; i < channels.length; i++) {
    if (
      channels[i].name.toLowerCase().indexOf('sport') !== -1 &&
      channels[i].mediaurl !== ''
    ) {
      let channel = {
        id: channels[i].id,
        name: channels[i].name,
        mediaurl: channels[i].mediaurl ? channels[i].mediaurl : 0
      };

      newChannelList.push(channel);
    }
  }

  return newChannelList;
}



