import React, { Component } from 'react';

class LoginErrorNotification extends Component {

  getErrorMesage() {
    switch (this.props.errorCode) {
      case "T_000":
        return "Das kommt in der Tat unerwartet. Überprüf deine Eingaben und versuch’s später nochmal.";
      case "S_200":
        return "Dein PIN wurde dreimal falsch eingegeben. Der Login wurde daher zur Sicherheit gesperrt.";
      case "T_201":
        return "Deine Eingabe war nicht korrekt. tKorrigiere deine Eingabe und versuch's nochmal.";
      case "T_202":
        return "Deine Daten wurden dreimal falsch eingegeben. Der Login wurde daher zur Sicherheit gesperrt.";
      case "T_205":
        return "Leider ist dieser Service gerade nicht verfügbar. Versuch’s später nochmal!";
      case "T_206":
        return "Du bist bereits auf einem anderen Gerät eingeloggt. Stop die Session und mach hier weiter.";
      case "T_207":
        return "Das System erkennt dich nicht. Bitte check deine Eingaben und versuche es erneut.";
      case "T_218":
        return "Kundennummer bitte erneut eingeben.";
      case "T_226":
        return "Aktuell bist du im Zahlungsverzug und hast hierzu einen Brief von uns erhalten. Bitte überweise den ausstehenden Betrag fristgerecht.";
      case "S_215":
        return "Bitte akzeptiere zuerst die Nutzungsbedinungen.";
      case "S_242":
        return "Es ist ein unbekannter Fehler aufgetreten";
      case "S_998":
        return "Sky Ticket ist leider aktuell nicht verfügbar.";
      case "S_999":
        return "Anmeldung aktuell leider nicht möglich";
      default:
        return "Es ist ein unbekannter Fehler aufgetreten";
    }
  };

  render() {
    return (
      <div className="login-error-container">
        {this.getErrorMesage()}
      </div>
    );
  }
}

export default LoginErrorNotification;


