import * as types from './notification-action-types'
import {notifications} from '../../utils/notification-symbols';

export function showPurchaseNotification() {
  return {
    type: types.SET_NOTIFICATION,
    notification: notifications.PURCHASE_NOTIFICATION
  }
}

export function showMinizapperNotification() {
  return {
    type: types.SET_NOTIFICATION,
    notification: notifications.MINIZAPPER_NOTIFICATION
  }
}

export function showLoginErrorNotification(errorCode) {
  return {
    type: types.SET_NOTIFICATION,
    notification: notifications["LOGIN_ERROR_" + errorCode]
  }
}

export function hideNotification() {
  return {
    type: types.SET_NOTIFICATION,
    notification: false
  }
}


