import * as types from './stage-action-types'
import * as configProd from './../../utils/config_prod.json'
import * as configTest from './../../utils/config_test.json'

import * as cloudExt from '../../vendor/cloud-ext';

export function setAppState(state, store) {

  if (store) {
    let appState = {
      "currentAsset": store.currentAsset,
      "isTicketPurchased": store.isTicketPurchased,
      "userData": store.userData,
      "appState": state
    };
    saveStateToPersistantStore(appState);
  }

  return {
    type: types.SET_APP_STATE,
    state : state
  }
}

export function loadConfiguration(enviroment) {
  const config = (enviroment === 'TEST') ? configTest : configProd;

  return {
    type: types.SET_CONFIGURATION,
    config : config
  }
}

export function saveStateToPersistantStore(store) {
  let appState = {
    "currentAsset": store.currentAsset,
    "isTicketPurchased": store.isTicketPurchased,
    "userData": store.userData,
    "appState": store.appState
  };

  cloudExt.setHistory(JSON.stringify(appState));

  return {
    type: types.SAVE_STATE
  }
}



