/*!
 * cloud-ext
 *
 * Copyright (c) 2017, Jack KIM.
 */

const hasCoTExt = (typeof CoTExt !== 'undefined');
const _userInfo = {
  user: null,
  pin: null,
  device: null
};


/**
 * Check Cloud Server or Chrome Browser
 *
 * @function isCloud
 */
const _isCloud = () => {
  // eslint-disable-next-line
  console.log('cloudExt.isCloud', hasCoTExt);
  return hasCoTExt;
};


/**
 * Get the stored User information on STB. (window.CoTExt.api.getUserInfo)
 *
 * @function getUserInfo
 * @param {cbGetUserInfo} cbGetUserInfo - The callback that handles the response.
 */
const _getUserInfo = (cbGetUserInfo) => {
  if (hasCoTExt) {
    window.CoTExt.api.getUserInfo(cbGetUserInfo);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.getUserInfo', cbGetUserInfo);
    setTimeout(() => {
      cbGetUserInfo(_userInfo);
    }, 100);
  }
};


/**
 * Store the user information to STB. (window.CoTExt.api.setUserInfo)
 *
 * @function setUserInfo
 * @param userInfo {object} Information about the user.
 * @param userInfo.user {string} The user id of the user.
 * @param userInfo.pin {string} The pin number of the user.
 * @param userInfo.device {string} The device-id of the user.
 */
const _setUserInfo = (userInfo) => {
  if (hasCoTExt) {
    window.CoTExt.api.setUserInfo(userInfo);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.setUserInfo', JSON.stringify(userInfo));
  }
};


/**
 * Launch 3'rd party service app. (window.CoTExt.api.launchService)
 *
 * @function launchService
 * @param {'SKY_VR'|'COT_YOUTUBE'|'ZDF'|'ARD'|'GAME'} serviceName - 3'rd party service app name <BR/><BR/>
 * * SKY_VR : SKY 360 VR Service <BR/>
 * * COT_YOUTUBE : CoT Youtube Service <BR/>
 * * ZDF : ZDF HbbTV Service <BR/>
 * * ARD : ARD HbbTV Service <BR/>
 * * GAME : Game Service  <BR/>
 */
const _launchService = (serviceName) => {
  if (hasCoTExt) {
    window.CoTExt.api.launchService(serviceName);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.launchService', serviceName);
  }
};


/**
 * Running CS Client Animation. (window.CoTExt.api.startAnimation) <BR/>
 * Refer to the animation guide doc. (Cloud UI Animation Guideline_v3(Eng).pptx)
 *
 * @function startAnimation
 * @param aniInfo {object} Information about cs client animation
 * @param aniInfo.effect {object} Animation Effect
 * @param aniInfo.effect.type {'SLIDE'} Fixed value
 * @param aniInfo.effect.isNextBlock {0|1} whether blocking the drawing of new images from CSS during the effect time or not
 (cf. Yes for the transition state, No for the simply focus changing) <BR/><BR/>
 * * 0 : Terminate the current animation effect instantly(End frame) then process the drawing <BR/>
 * * 1 : Blocking(stand by) the drawing , process it at one time after the effect finish
 * @param aniInfo.effect.isRefresh {0|1|2|3} whether the preserve of ex-contents which are on the area to apply the effects or not <BR/><BR/>
 * * 0 : New Image and Old Image are moving together(New Image-Slide In / Old Image-Slide Out) <BR/>
 * * 1 : Old Image area is cleared, New Image is only moved (New Image-Slide In / Old Image-Clear Rect) <BR/>
 * * 2 : Old Image area is fixed, New Image is only moved (New Image-Slide In / Old Image-None) <BR/>
 * * 3 : New Image is drawn first at the End location without effect, Old Image is only moved from End to Start <BR/>
 * @param aniInfo.effect.duration {number} time to applying the effect (unit : msec)
 * @param aniInfo.effect.accel {number} acceleration(under development)
 * @param aniInfo.start {object} Animation start information
 * @param aniInfo.start.x {number} X-coordinate of motion start
 * @param aniInfo.start.y {number} Y-coordinate of motion start
 * @param aniInfo.start.w {number} Width of motion start
 * @param aniInfo.start.h {number} Height of motion start
 * @param aniInfo.start.z {0} Fixed value(As Layer Index will be defined later)
 * @param aniInfo.start.a {number} Alpha value of motion start (1: 100%, 0.5: 50%, 0: transparent)
 * @param aniInfo.end {object} Animation end information
 * @param aniInfo.end.x {number} X-coordinate of motion end
 * @param aniInfo.end.y {number} Y-coordinate of motion end
 * @param aniInfo.end.w {number} Width of motion end
 * @param aniInfo.end.h {number} Height of motion end
 * @param aniInfo.end.z {0} Fixed value(As Layer Index will be defined later)
 * @param aniInfo.end.a {number} Alpha value of motion end (1: 100%, 0.5: 50%, 0: transparent)
 */
const _startAnimation = (aniInfo, isRefresh) => {
  if (hasCoTExt) {
    window.CoTExt.api.startAnimation(aniInfo, isRefresh);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.startAnimation', JSON.stringify(aniInfo));
  }
};


/**
 * Store the history information to STB. (window.CoTExt.api.setHistory)
 *
 * @function setHistory
 * @param history {string} - History data
 */
const _setHistory = (history) => {
  if (hasCoTExt) {
    window.CoTExt.api.setHistory(history);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.setHistory', history);
  }
};


/**
 * The addEventListener() method attaches an event handler. (window.CoTExt.api.addEventListener)
 *
 * @function addEventListener
 * @param {'onMove'|'onTimeout'} event -  A String that specifies the name of the event.
 * @param {cbAddEventListener} cbAddEventListener -  Specifies the function to run when the event occurs.
 */
const _addEventListener = (event, cbAddEventListener) => {
  if (hasCoTExt) {
    window.CoTExt.api.addEventListener(event, cbAddEventListener);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.addEventListener', event, cbAddEventListener);
  }
};


/**
 * Get DeviceId. (window.CoTExt.api.getDeviceId)
 *
 * @function getDeviceId
 * @param {cbGetDeviceId} cbGetDeviceId - The callback that handles the response.
 */
const _getDeviceId = (cbGetDeviceId) => {
  if (hasCoTExt) {
    window.CoTExt.api.getDeviceId(cbGetDeviceId);
  } else {
    // eslint-disable-next-line
    console.log('cloudExt.getDeviceId', cbGetDeviceId);
    setTimeout(() => {
      cbGetDeviceId('Cloud_DeviceId');
    }, 100);
  }
};


module.exports = {
  isCloud: _isCloud,
  getUserInfo: _getUserInfo,
  setUserInfo: _setUserInfo,
  launchService: _launchService,
  startAnimation: _startAnimation,
  setHistory: _setHistory,
  addEventListener: _addEventListener,
  getDeviceId: _getDeviceId
};



