import React, { Component } from 'react';
import {appStates} from '../../utils/app-state-symbols';

import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'

import LoadingScreenComponent from '../../components/loading-screen/loading-screen-component'

class LoadingScreenContainer extends Component {
  _timeoutId = null;

  componentDidMount() {
    const that = this;
    this._timeoutId = setTimeout(function () {
      if (that.props.currentAsset !== 'Sky Sport News HD') that.props.showMinizapperNotification();
      that.props.setAppState(appStates.PLAYBACK, this.props);
    }, 2000);
  }

  componentWillUnmount() {
    if (this._timeoutId !== null) {
      clearTimeout(this._timeoutId);
      this._timeoutId = null;
    }
  }

  render() {
    return (
      <LoadingScreenComponent eventIsLoading={this.props.currentAsset.title !== 'Sky Sport News HD'} />
    );
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LoadingScreenContainer);



