import createReducer from '../../lib/create-reducer'
import * as types from './event-list-action-types'
import { adaptEventItems } from '../../adapters/event-list-adapter'
import { adaptHistoryEventItems } from '../../adapters/history-event-list-adapter'

export const eventList = createReducer([], {
  [types.GET_EVENT_LIST](state, action) {
    return adaptEventItems(action.payload);
  }
});

export const selectedEventListItem = createReducer(0, {
  [types.SET_SEL_EVENT_LIST_INDEX](state, action) {
    return action.index
  },
});


