import * as types from './video-action-types'
import axios from 'axios';

export function setCurrentAsset(asset, channelList) {
  const request = axios.get(`http://skyticket.sky.de/st/multiplatform/ipad/json/details/asset/${asset.id}.json`);

  return (dispatch) => {
    request.then(({data}) => {
      data.asset.assetId = 0;
      
      for (let i = 0; i < channelList.length; i++) {
        if (channelList.mediaurl === data.media_url) {
          data.asset.assetId = channelList[i].id;
          break;
        }
      }

      dispatch({type: types.SET_ASSET, payload: data})
    });
  }
}

export function setCurrentAssetfromHistory(asset) {
  return {
    type: types.SET_ASSET_FROM_HISTORY,
    payload: asset
  }
}

export function getSSNHD(url) {
  const request = axios.get(url);

  return (dispatch) => {
    request.then(({data}) => {
      dispatch({type: types.LOAD_SSNHD, payload: data})
    });
  }
}

export function getChannelList(url) {
  const request = axios.get(url);

  return (dispatch) => {
    request.then(({data}) => {
      dispatch({type: types.LOAD_CHANNEL_LIST, payload: data})
    });
  }
}


