import { getDateFromSILKDate } from '../utils/date-helper';

export function adaptEventDetailItem(event) {
  const item = event.asset;

  const silkStartDate = item.on_air.start_date;
  const silkStartTime = item.on_air.start_time;
  const silkEndDate = item.on_air.end_date;
  const silkEndTime = item.on_air.end_time;
  const startDate = getDateFromSILKDate({
    date: silkStartDate,
    time: silkStartTime
  });
  const endDate = getDateFromSILKDate({
    date: silkEndDate,
    time: silkEndTime
  });

  let ticketEndDate = new Date();
  ticketEndDate.setDate(new Date().getDate() + 1);
  ticketEndDate.setHours(7);
  ticketEndDate.setMinutes(25);
  ticketEndDate.setSeconds(0);

  const title = item.title;
  const streamUrl = item.media_url;
  const assetId = item.assetId;
  // const apixId = item.apix_id;
  const apixId = 99202657;

  let imgUrl = '';

  for (let i = 0; i < item.main_picture.picture.length; i++) {
    const img = item.main_picture.picture[i];
    if (img.width > img.height) {
      if (img.width > 316 && img.height > 177) {
        imgUrl =
          'https://skyticket.sky.de' +
          item.main_picture.picture[i].path +
          '/' +
          item.main_picture.picture[i].file;
        break;
      }
    }
  }

  const newItem = {
    startDate,
    endDate,
    title,
    imgUrl,
    streamUrl,
    apixId,
    assetId
  };

  return newItem;
}



