import React from 'react';

class EventListItemComponent extends React.Component {
  friendlytimeTime(date) {
    const hours = date.getHours();
    let minutes = date.getMinutes();
    minutes = (minutes < 10) ? '0' + String(minutes) : minutes;
    return `${hours}:${minutes} Uhr`
  }

  checkForStartsSoon() {
    const now = new Date();
    const timeDiff = Math.abs(this.props.event.startDate.getTime() - now.getTime());
    const diffHours = Math.ceil(timeDiff / (1000 * 3600)) - 1;
    if (diffHours > 0) return '';
    else return 'EventItem-badge-red';
  }

  timeToEvent() {
    const now = new Date();
    const timeDiff = Math.abs(this.props.event.startDate.getTime() - now.getTime());

    const diffMinutes = (Math.ceil(timeDiff / (1000 * 60)) % 60);
    const diffHours = Math.ceil(timeDiff / (1000 * 3600)) - 1;

    if (
      now.getTime() > this.props.event.startDate.getTime() &&
      now.getTime() < this.props.event.endDate.getTime()
    ) return 'Läuft gerade';
    else if (diffHours === 1) return 'In einer Stunde';
    else if (diffHours > 1) return `In ${diffHours} Stunden`;
    else return `In ${diffMinutes} Minuten`;
  }

  render() {
    return (
      <div className={`EventListItem-container EventItem-container ${this.props.selected ? 'selected' : ''}`} >
        <div className="img-container">
          <img className="EventListItem-graphic EventItem-graphic" src={this.props.event.imgUrl} alt=""/>
        </div>
        <div className="EventListItem-info EventItem-info">
          <div className="EventItem-text">
            <span className={(this.props.event.endDate) ? 'EventItem-time' : ''}>
              { this.friendlytimeTime(this.props.event.startDate) }
              <span className="EventItem-endtime">
                {' '}- { this.friendlytimeTime(this.props.event.endDate) }
              </span>
            </span>
              <span className="EventItem-badges">
                <span className={`EventItem-badge ${ (this.props.event.startDate) ? this.checkForStartsSoon() : ''}`}>
                {(this.props.index === 0) ? 'Läuft gerade' : this.timeToEvent() }
              </span>
            </span>
            <div className="EventItem-title">{this.props.event.title}</div>
          </div>
          <div className="EventListItem-order">
            {(this.props.ticketAquired || this.props.index === 0) ? 'Einschalten' : ((this.props.ticketAvalible) ? 'Jetzt Tagesticket buchen!' : 'Kein Tagesticket verfügbar!')}
          </div>
        </div>
      </div>
    );
  }
}

export default EventListItemComponent;



