import React, { Component } from 'react';

class MinizapperNotification extends Component {
  constructor(props) {
    super(props);

    this.state = {
      logo: '',
      startTime: '',
      endTime: '',
      
      title: '',
      progress: ''
    }
  }

  componentDidMount() {
    this.setState({
      logo: this.props.currentAsset.imgUrl,
      startTime: this.getFriendlyTime(this.props.currentAsset.startDate),
      endTime: this.getFriendlyTime(this.props.currentAsset.endDate),
      title: this.props.currentAsset.title,
      progress: this.calculateProgress()
    })
  }

  calculateProgress() {

  }

  getFriendlyTime(d) {
    return (d !== null) ? `${d.getDate()}:${d.getMinutes()}` : null;
  }

  render() {
    return (
      <div className="Minizapper-container">
        <img
          className="Minizapper-channellogo"
          src={this.state.logo}
          alt=""
        />
        <div className="Minizapper-broadcastinfo">
          <div className="Minizapper-textcontainer">
            <div className="Minizapper-broadcasttime">
              {this.state.startTime} - {this.state.endTime}
            </div>
            <div className="Minizapper-broadcasttitle">
              {this.state.title}
            </div>
          </div>
          <div className="Minizapper-progressbar">
            <div
              className="Minizapper-progressbar-fill"
              style={{ width: this.state.progress * 100 + '%' }}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default MinizapperNotification;



