import React from 'react';
import {bindActionCreators} from 'redux'
import {ActionCreators} from '../../actions'
import {connect} from 'react-redux'
import LoginComponent from '../../components/login/login-component';

import {appStates} from '../../utils/app-state-symbols';

class LoginContainer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      customerCode: null,
      loginInProgress: false
    };

    this.setCustomerCode = this.setCustomerCode.bind(this);
    this.login = this.login.bind(this);
    this.loginCallback = this.loginCallback.bind(this);
    this.backHandler = this.backHandler.bind(this);
  }

  setCustomerCode(cCode) {
    this.setState({
      customerCode: cCode
    })
  }

  login(pin) {
    this.setState({loginInProgress: true});
    let loginPath = this.props.config.Endpoints.SILK.domain + this.props.config.Endpoints.SILK.login;
    this.props.login(loginPath, this.state.customerCode, pin, this.props.deviceId, false, this.loginCallback);
  }

  backHandler() {
    this.setState({
      customerCode: null
    })
  }

  loginCallback(result) {
    if (result.resultMessage === 'OK')  {
      this.setState({ loginFailed: false });
      this.props.setAppState(appStates.LOADING, this.props);
    }
    else {
      this.setState({
        loginInProgress: false,
        customerCode: null
      })
    }
  }

  render() {
    return (
      <LoginComponent
        step={(this.state.customerCode === null) ? 0 : ((this.state.loginInProgress) ? 2 : 1)}
        setCustomerCode={this.setCustomerCode}
        setPin={this.login}
        backHandler={this.backHandler}
      />
    );
  }
}

function mapStateToProps(state) {
  return state
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LoginContainer);



