import * as types from './purchase-action-types'
import axios from 'axios';

export function getAvalibleTicket(url) {
  const request = axios.get(url);

  return (dispatch) => {
    request.then(({data}) => {
      dispatch({type: types.GET_TICKETS_LIST, payload: data})
    });
  }
}

export function purchaseTicket(url, skygoSessionId, ticket, callback) {
  const details = {
    skygoSessionId: skygoSessionId,
    passName: ticket.title,
    price: ticket.price,
    eventId: ticket.event_id,
    eventTrasmDate: `${ticket.validity.start_date}.${ticket.validity.start_time}`,
    eventEndDate: `${ticket.validity.end_date}.${ticket.validity.end_time}`,
    packageCode: ticket.entitlements,
    category: ticket.category,
    platform: "web",
    product: "ST",
    version: 1234
  }

  return (dispatch) => {
    fetch(url, {
      method: "POST",
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: Object.keys(details) .map(key=>encodeURIComponent(key)+'='+encodeURIComponent(details[key])) .join('&')
    }).then(response => {
      return response.json().then(responseObj => {
        callback((responseObj.resultCode === "T_100"));
        dispatch({type: types.SET_TICKET_PUCHASED, payload: (responseObj.resultCode === "T_100")})
      })
    })
  }
}

export function setTicketPurchased() {
  return {
    type: types.SET_TICKET_PUCHASED,
    payload: true
  }
}







