import React from 'react';
import DigitEntryComponent from '../digit-entry/digit-entry-component';

class LoginComponent extends React.Component {
  getContentForStep() {
    if (this.props.step === 0) {
      return (
        <DigitEntryComponent
          caption={"Bitte geben Sie Ihre Sky Business Kundennummer ein."}
          numberOfDigits={10}
          isPassword={false}
          digitsEnteredHandler={this.props.setCustomerCode}
          backHandler={function() {}}
        />
      )
    } else if(this.props.step === 1) {
      return (
        <DigitEntryComponent
          caption={"Bitte geben Sie Ihre Sky Business PIN ein, um den Login abzuschließen."}
          numberOfDigits={4}
          isPassword={true}
          digitsEnteredHandler={this.props.setPin}
          backHandler={this.props.backHandler}
        />
      )
    } else {
      return null;
    }
  }

  render() {
    return (
      <div className="login-component">
        {this.getContentForStep()}
      </div>
    );
  }
}

export default LoginComponent;



