export function getDateFromSILKDate(silkdate) {
  const dateList = silkdate.date.split('/');
  const timeList = silkdate.time.split(':');

  return new Date(
    Number(dateList[0]),
    Number(dateList[1]) - 1,
    Number(dateList[2]),
    Number(timeList[0]),
    Number(timeList[1])
  );
}



